import React from "react";
import { Link, graphql } from "gatsby";
import Layout from "../components/Layout";
import * as styles from '../styles/home.module.css';

import logo from "../images/Chefs Choice Logo.png";
import homeBannerImage from "../images/homeBannerImage.png";
import whiteArrow from "../images/whiteArrow.png";

import kedi1 from "../images/Kedi 400GR/Kedi1.png";
import kedi2 from "../images/Kedi 400GR/Kedi2.png";
import kedi3 from "../images/Kedi 400GR/Kedi3.png";
import kedi4 from "../images/Kedi 400GR/Kedi4.png";
import kedi5 from "../images/Kedi 400GR/Kedi5.png";
import kedi6 from "../images/CC - Mother & Babycat.png";
 
import kopek1 from "../images/Kopek 400GR/Kopek1.png";
import kopek2 from "../images/Kopek 400GR/Kopek2.png";
import kopek3 from "../images/Kopek 400GR/Kopek3.png";
import kopek4 from "../images/Kopek 400GR/Kopek4.png";
import kopek5 from "../images/Kopek 400GR/Kopek5.png";
import kopek6 from "../images/Kopek 400GR/Kopek6.png";
import kopek7 from "../images/Kopek 400GR/Kopek7.png";
import kopek8 from "../images/Kopek 400GR/Kopek8.png";

import promoted1 from "../images/promoted1.jpeg";

export default function Home({ data }) {

  // Window Undefined Handle
  // if(typeof window === "undefined") {
  //   return;
  // }

  // Language 
  const [language, setLanguage] = React.useState('');
  React.useEffect(() => {
    setLanguage(window.localStorage.getItem('CC-Language'));
  }, []);


  // Fetch data
  const logos = data.allContentfulCustomerLogos.nodes[1].chefsChoiceCustomerLogos;
  const logosLength = (logos.length*window.innerWidth)/10;


  let dogArray = [kopek1, kopek2, kopek3, kopek6, kopek7];
  let catArray = [kedi1, kedi2, kedi3, kedi4, kedi5];


  // Carousel
  React.useEffect(() => {

    const carousel1 = document.getElementById("carousel1");
    carousel1.style.width = logosLength + 'px';
    const carousel2 = document.getElementById("carousel2");
    carousel2.style.width = logosLength + 'px';
    let interval = 0;

    
    const myIntervalCarousel = setInterval(function () {
      carousel1.style.left = interval + "px";
      carousel2.style.left = interval - logosLength + "px";
      interval += 0.5;
      if(interval >= logosLength) {
        interval = 0;
      }
      return () => clearInterval(myIntervalCarousel);
    }, 3);
  }, []);

  // Scroll
  const [scrollPosition, setScrollPosition] = React.useState(0);
  const updatePosition = React.useCallback(() => {
    setScrollPosition(window.pageYOffset);
    

    if (scrollPosition > 300) {      
      document.getElementById("whiteArrow").style.display = 'none';
    } else {
      document.getElementById("whiteArrow").style.display = 'block';
    }

  }, [window.pageYOffset]);

  React.useEffect(() => {

    window.addEventListener("scroll", updatePosition);
    return () => window.removeEventListener("scroll", updatePosition);

  }, [updatePosition]);


  // Fetch Instagram
  const [instaObj, setInstaObj] = React.useState([]);

  React.useEffect(() => {
    async function fetchData() {
      const response = await fetch("https://feeds.behold.so/hqREjbOqWXierU0OAifY");
      const data = await response.json();
      setInstaObj(data);
    };

    fetchData();

  }, [])


  return (
    <Layout>
      <div className={styles.Home} id="home">

        <img src={logo} alt="logo" className={styles.logo}/>

        <div className={styles.banner}>
          <div className={styles.bannerTextContainer}>
            <h3 className={styles.bannerHeading}>{language === 'tr-TR' ? 'DOSTUNUZUN EVDE BİR CHEF’İ VAR.' : 'YOUR PETS HAVE A CHEF AT HOME'}</h3>
            <p style={{fontFamily: "sans-serif"}}>
            {language === 'tr-TR' ? 
            "Chef’s Choice’un tarifleri, şef ve beslenme uzmanları tarafından özenle formüle edilmiştir. %100 taze etler ve doğal içeriklerle hazırlanan bu mamalar, şeker, tuz, renklendirici, aroma veya koruyucu içermez. Dostunuzun ağzını sulandıracak pürüzsüz pate kıvamındaki tariflerimiz, vitamin, mineral ve diğer besinlerle zenginleştirilmiştir. " 
            : 
            "Chef’s Choice recipes are formulated by chefs and nutritionists, made with 100% fresh meat and natural ingredients and are free from sugar, salt, artificial colors, aromas or preservatives. These smoothly and intensely flavored recipes are enhanced with vitamins, minerals and other nutrients to make this meal irresistible for your pets."}
            </p>
            <Link to="/products" className={styles.bannerButton}>{language === 'tr-TR' ? "GÖZ GEZDİR" : "BROWSE PRODUCTS"}</Link>
          </div>
          <img src={homeBannerImage} alt="kedi ve kopek resmi" className={styles.homeBannerImage}/>
        </div>

        <img src={whiteArrow} alt="white arrow" className={styles.whiteArrow} id="whiteArrow"/>

        <div className={styles.refLogos} id="carousel1">
          {logos.map(logo => 
            <img src={logo.url} className={styles.refLogo} alt="referance logo" key={String(logo.url)}/>
          )}
        </div>
        <div className={styles.refLogos} id="carousel2">
          {logos.map(logo => 
            <img src={logo.url} className={styles.refLogo} alt="referance logo" key={String(logo.url) + 2}/>
          )}
        </div>


        <div className={styles.productsContainer}>
            <div className={styles.products}>
              {dogArray.map(dog => 
                <img src={dog} className={styles.product} alt="dog food" key={String(dog)} />
              )}
              <h3 className={styles.headingProduct}>{language === 'tr-TR' ? "KÖPEKLER İÇİN" : "FOR DOGS"}</h3>
              <p style={{color: 'black', fontFamily: 'sans-serif'}}>
              {language === 'tr-TR' ? 
              "Köpekleriniz için özenle hazırlanmış enfes taze etli yaş mamalar." 
              : 
              "Delicious fresh meat paté’s prepared with care for your dogs."}
              </p>
              <Link to="/products?dog" className={styles.bannerButton}>{language === "tr-TR" ? "KEŞFET" : "BROWSE"}</Link>
            </div>
            <div className={styles.products}>
              {catArray.map(cat => 
                <img src={cat} className={styles.product} alt="cat food" key={String(cat)} />
              )}
              <h3 className={styles.headingProduct}>{language === 'tr-TR' ? "KEDİLER İÇİN" : "FOR CATS"}</h3>
              <p style={{color: 'black', fontFamily: 'sans-serif'}}>
              {language === 'tr-TR' ? 
              "Kedileriniz için özenle hazırlanmış enfes taze etli yaş mamalar." 
              : 
              "Delicious fresh meat paté’s prepared with care for your cats."}
              </p>
              <Link to="/products?cat" className={styles.bannerButton}>{language === "tr-TR" ? "KEŞFET" : "BROWSE"}</Link>
            </div>
        </div>

        <div className={styles.promotedContainer}>
            <div className={styles.imageCropper}>
              <img src={promoted1} alt="product image" className={styles.promotedImage}/>
            </div>
            <div className={styles.promotedTextContainer}>
              <h3 className={styles.promotedHeading}>{language === "tr-TR" ? "KABAK SERİSİ" : "PUMPKIN LINE"}</h3>
              <p style={{fontFamily: "sans-serif"}}>
              {language === 'tr-TR' ? 
              "Patili dostlarımızın yemekleri için balkabağından daha iyi ne olabilir ki! Bu süper besinin köpekler için bir çok faydası vardır. Mamalarına lezzet katarken, onlara gerekli mikro besinleri ve lifleri sağlar.  Balkabağı, demir ve potasyum gibi gerekli minerallerle, A, C ve E vitaminleri ile doludur. Prebiyotik işlevi sayesinde köpeğinizi balkabağı ile formüle edilmiş bu mamalarla beslemek, sindirim sağlığını güçlendirmesine ve ishal ile ilgili sorunları azalmasına yardımcı olacaktır." 
              : 
              "What better ingredient for our buddies’ meals than pumpkin! This superfood has numerous benefits for dogs. While being a tasteful addition to their foods, it provides them with essential micronutrients and fibers. Pumpkin is filled with vitamins A, C, and E, along with necessary minerals such as iron and potassium. Due to its prebiotic function, feeding your dog with a pumpkin based food will also help them strengthen their digestive health, and reduce issues with diarrhea. "}
              </p>
              <Link to="/products" className={styles.promotedButton}>{language === 'tr-TR' ? "BİLGİ EDİN" : "LEARN MORE"}</Link>
            </div>
        </div>

        <div className={styles.aboutContainer}>
            <div className={styles.aboutTextContainer}>
              <h3 className={styles.aboutHeading}>{language === 'tr-TR' ? "HAKKIMIZDA" : "ABOUT US"}</h3>
              <p style={{fontFamily: "sans-serif"}}>
              {language === 'tr-TR' ? 
              "Chef's Choice olarak,  lezzet ve besin değerlerini ön planda tutarak kedi ve köpekler için mükemmel mamayı yaratmayı hedefleyen şefler, beslenme uzmanları, veterinerler ve hayvan severlerden oluşan bir aileyiz. %100 taze et ile formüle edilen yaş mamalarımız, doğal ve sağlıklı malzemelerle üretilmiştir. Farklı tariflerlerimizle, kedi ve köpeklerinizin beslenme ihtiyaçlarını karşılarken farklı lezzetler keşfetmesine yardımcı olmayı hedefliyoruz." 
              : 
              "As Chef’s Choice, we are a family of chefs, nutritionists, veterinarians, and pet lovers aiming to create the perfect food for cats and dogs by prioritizing taste and nutritional values. Our paté’s, formulated with 100% fresh meat, are made with natural and wholesome ingredients. Through our selection of recipes, we aim to help your buddy explore different tastes while fulfilling their nutritional needs. "}
              </p>
              <Link to="/about" className={styles.aboutButton}>{language === 'tr-TR' ? "BİLGİ EDİN" : "LEARN MORE"}</Link>
            </div>
            <div className={styles.imageCropper}>
              <img src={logo} alt="product image" className={styles.aboutImage}/>
            </div>
        </div>

        <div className={styles.promotedContainer}>
            <div className={styles.imageCropper}>
              <img src={kedi6} alt="product image" className={styles.promotedImage}/>
            </div>
            <div className={styles.promotedTextContainer}>
              <h3 className={styles.promotedHeading}>{language === "tr-TR" ? "ANNE VE YAVRU KEDİ" : "MOTHER & BABYCAT"}</h3>
              <p style={{fontFamily: "sans-serif"}}>
              {language === 'tr-TR' ? 
              "Chef's Choice olarak hamile ve emziren kedilerin ve yavru kedilerin beslenmesine çok önem veriyoruz. Daha hassas, besleyici, sindirilebilir, tüketimi kolay bir gıdaya ihtiyaçları olduğunun farkındayız. Formüllerimiz, yavru kedilerin gelişimini desteklemek ve hamile ve emziren kedilerin ihtiyaç duyduğu ekstra besinleri sağlamak için antioksidanlar ve vitaminler açısından zengindir. Yaş mamalarımızın pürüzsüz ve yumuşak yapısı, kedilerin sütten katı mamaya geçişlerine destek olur." 
              : 
              "As Chef’s Choice, we highly care about the nutrition of pregnant and nursing cats, and kittens. We are aware that they are in need of a more sensitive, nutritious, and digestible food, with a soft texture to make it easy to consume. Our formulas are rich in antioxidants and vitamins to support the development of young kittens, and provide the extra nutrition that pregnant and nursing cats need. The smooth mousse texture of our patés aid kittens in their transition from milk to solid foods."}
              </p>
              <Link to="/products" className={styles.promotedButton}>{language === 'tr-TR' ? "BİLGİ EDİN" : "LEARN MORE"}</Link>
            </div>
        </div>

        <div className={styles.instagramContainer}>
            {instaObj.map(post => 
              <a href={post.permalink} target="_blank" rel="noreferrer" className={styles.post}>
                <img src={post.mediaUrl} alt="instagram post" key={post.id} className={styles.instagramPhoto}/>
                <p className={styles.postText}>{post.caption}</p>
              </a>)}
            <a href="https://www.instagram.com/chefschoicetr/" target="_blank" rel="noreferrer" className={styles.instaHandle}>@chefschoicetr</a>
            <a href="https://www.instagram.com/chefschoicetr/" target="_blank" rel="noreferrer" className={styles.instaHandle2}>@chefschoicetr</a>
        </div>

        <div className={styles.whiteBackground}></div>
        <div className={styles.whiteBackground2}></div>
        <div className={styles.whiteBackground3}></div>
        
      </div>
    </Layout>
  )
}

export const query = graphql`
  query Logos {
    allContentfulCustomerLogos {
      nodes {
        chefsChoiceCustomerLogos {
          url
        }
      }
    }
  }
`
